import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import {
  Form,
} from "react-bootstrap";
import { FieldError, IMAGES } from "../../assets";
import { ToastContainer, toast } from "react-toastify";
import { useCookies } from "react-cookie";
import { PATH, TOASTER_STYLING_VALUES } from "../../config";
import { Link, useNavigate } from "react-router-dom";
import { Header } from "../LandingPage/Header";

export default function Login() {
  let history = useNavigate();
  const { register, handleSubmit, errors } = useForm();
  const [cookies, setCookies] = useCookies();
  useEffect(() => {
    document.title = "Login || Sama Health";
  }, []);

  function onSubmit(data) {
    if (data.email === "doctor@gmail.com") {
      data = { ...data, token: "token", userType: "doctor" };
      setCookies("sama_health_user", data);
      history(PATH.DASHBOARD);
    }
    else {
      toast.error("Invalid Email or Password", TOASTER_STYLING_VALUES);
    }
  }
  console.log("cookies", cookies);
  return (
    <React.Fragment>
      <ToastContainer />
      <Header />
      <section className="login-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-5 col-md-7 col-sm-12 col-12">
              <div className="card">
                <div className="card-body">
                  <div className="text-center">
                    <img src={IMAGES.GREEN_LOGO} className="img-fluid" alt="sama-logo" />
                  </div>
                  <Form onSubmit={handleSubmit(onSubmit)}>
                    <Form.Group className="inputContainer">
                      <Form.Control
                        type="email"
                        name="email"
                        placeholder="Email"
                        style={{ borderColor: (errors?.email) ? "#a80000" : "" }}
                        ref={register({
                          required: true,
                          pattern: /[^\s]+/,
                        })}
                      />
                      <Form.Label>Email</Form.Label>
                      {errors?.email && (
                        <FieldError message={"This Field is Required"} />
                      )}
                    </Form.Group>
                    <Form.Group className="inputContainer">
                      <Form.Control
                        type="password"
                        name="password"
                        placeholder="Password"
                        style={{ borderColor: (errors?.password) ? "#a80000" : "" }}
                        ref={register({
                          required: true,
                          pattern: /[^\s]+/,
                        })}
                      />
                      <Form.Label>Password</Form.Label>
                      {errors?.password && (
                        <FieldError message={"This Field is Required"} />
                      )}
                    </Form.Group>
                    <Form.Group className="text-end">
                      <Link to="#">Forgot Password?</Link>
                    </Form.Group>
                    <Form.Group className="inputContainer">
                      <button type="submit">
                        Log In
                      </button>
                    </Form.Group>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </React.Fragment >
  );
}