import React from "react";
import { Container, Nav, Navbar } from "react-bootstrap";
import { IMAGES } from "../../assets";
import { PATH } from "../../config";

export function Header() {
    return (
        <React.Fragment>
            <Navbar collapseOnSelect expand="lg" className="login-header" fixed="top">
                <Container>
                    <Navbar.Brand href={PATH.LANDING_PAGE}>
                        <img src={IMAGES.SAMA_WHITE_LOGO} className="img-fluid" alt="sama_logo" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="m-auto middle-tabs">
                            <Nav.Link href="#features">What</Nav.Link>
                            <Nav.Link href="#features">Why</Nav.Link>
                            <Nav.Link href="#features">How</Nav.Link>
                            <Nav.Link href="#features">For Therpist</Nav.Link>
                            <Nav.Link href="#features">For Member</Nav.Link>
                            <Nav.Link href="#pricing">Contact Us</Nav.Link>
                        </Nav>
                        <Nav>
                            <Nav.Link href={PATH.LOGIN} className="login_btn">
                                Log in as the therapist
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </React.Fragment>
    )
}
